<template>
    <button @click="btnClick">등록</button>
</template>

<script>
export default {
  name: 'Button',
  data () {
    return {
      title: '제목을 입력해주세요',
      value: null
    }
  },
  methods: {
    btnClick () {
      alert('버튼클릭')
    }
  }
}
</script>



