export default {
  namespaced: true,
  state: {
    list: [],
    result: []
  },
  getters: {
    getItem: (state) => (index) => state.list[index].model
  },
  mutations: {
    addTool (state, obj) { state.list.push(obj) },
    removeTool (state, index) { state.list.splice(index, 1) },
    toggleTool (state, index) {
      let obj = state.list[index]
      obj.model.toggle = !obj.model.toggle
      state.list.splice(index, 1, obj)
    },
    saveItem (state) { state.result = state.list },
    setItem (state, param) {
      let tool = state.list[param.index].model
      tool.value.push(param.value)
    },
    removeItem (state, param) {
      let tool = state.list[param.rootIdx].model
      tool.value.splice(param.valueIdx, 1)
    }
  },
  actions: {
    /*
    addItem: ({commit}, obj) => commit('_add', obj)
    */
  }
}



