<template>
   <div class="workspace">
     <button @click="saveItem">저장</button>
     <div v-for="(item, index) in  tools"  :key="index">
      <button @click="removeTool(index)">삭제</button>
      <component :is="item.component" :index="index"></component>
      </div>
   </div>
</template>

<script>
import {mapState, mapMutations} from 'vuex'

export default {
  name: 'WorkSpace',
  created () {
    this.$res('component-add', (type) => {
      let obj = {
        component: null,
        model: {
          toggle: true,
          title: '제목을 입력하세요',
          value: []
        }
      }
      switch (type) {
        case 'text': obj.component = 'RenderTextBox'; break
        case 'radio': obj.component = 'RenderRadioBox'; break
      }
      this.addTool(obj)
    })
  },
  components: {
    RenderRadioBox: () => import('../render/RenderRadioBox.vue'),
    RenderTextBox: () => import('../render/RenderTextBox.vue')
  },
  methods: {
    ...mapMutations({
      addTool: 'tool/addTool',
      removeTool: 'tool/removeTool',
      saveItem: 'tool/saveItem'
    })
  },
  computed: {
    ...mapState({
      tools: state => state.tool.list,
      result: state => state.tool.result
    })
  }
}
</script>

<style scoped>
  .workspace {
    margin: 60px 30px;
    border: solid 1px;
    width: 30%;
    height: 700px;
    float: left;
    text-align: center;
  }
   .workspace > button {
     position: relative;
     left : 256px;
     top : 10px;
   }
</style>



