<template>
  <div class="tool-container">
      <h1>설문지 등록페이지</h1>
      <ToolSpace/>
      <WorkSpace/>
      <router-link to="/">돌아가기</router-link>
  </div>
</template>

<script>
import ToolSpace from './tool/space/ToolSpace.vue'
import WorkSpace from './tool/space/WorkSpace.vue'

export default {
  name: 'ToolContainer',
  data () {
    return {
      msg: '도구 컨테이너'
    }
  },
  components: {
    ToolSpace,
    WorkSpace
  }
}
</script>

<style scoped>
  .tool-container {
    margin: 10px 10px 10px 10px;
    padding: 10px 10px 10px 10px;
  }
</style>



