<template>
  <div class="survey-list-container">
      <h1>설문지 목록</h1>
      <grid :head-array="heads" :data-array="datas" />
  </div>
</template>

<script>
import Grid from '@/components/common/Grid'

export default {
  name: 'SurveyListContainer',
  data () {
    return {
      heads: [
        '순번',
        '설문지이름',
        '설문지대상',
        '등록자',
        '등록날짜',
        '기간'
      ],
      datas: []
    }
  },
  created () {
    const config = {
      json: true,
      headers: { 'Content-Type': 'application/json;charset=UTF-8' }
    }
    const data = {
      data1: '1',
      data2: '2'
    }
    console.log(JSON.stringify(data))

    this.axios.post('/action/management/list.do', JSON.stringify(data), config).then((response) => {
      console.log(response.data)
    })
  },
  components: {
    Grid
  }
}
</script>

<style scoped>
  .survey-list-container {
    margin: 10px 10px 10px 10px;
    padding: 10px 10px 10px 10px;
  }
</style>



