<template>
  <div class="toolspace">
        <h1>{{ msg }}</h1>
        <ul>
            <li><TextBox/></li>
            <li><RadioBox/></li>
        </ul>
        <div><Button/></div>
  </div>
</template>

<script>
import TextBox from '../render/TextBox.vue'
import RadioBox from '../render/RadioBox.vue'
import Button from '../action/Button.vue'

export default {
  name: 'ToolSpace',
  data () {
    return {
      msg: '도구 컨테이너',
      title: '제목을 입력해주세요',
      value: null
    }
  },
  components: {
    TextBox,
    RadioBox,
    Button
  }
}
</script>

<style scoped>
.toolspace {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    text-align: center;
    margin-top: 60px;
    border: solid 1px;
    width: 20%;
    height: 700px;
    float: left;
}

.toolspace ul{
    padding: 10px;
}

.toolspace ul li {
    list-style: none;
    margin-top: 40px;
    text-align: left;
    padding: 10px;
    border: solid 1px;
}
</style>



