<template>
  <div class="data-grid">
    <table>
      <thead>
      <tr>
        <th v-for="(head, index) in headArray" :key="index">{{head}}</th>
      </tr>
      </thead>
      <tbody>
        <tr>
          <td v-for="(data, index) in dataArray" :key="index">{{data}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>

export default {
  name: 'Grid',
  props: ['headArray', 'dataArray']
}
</script>

<style scoped>
  .tool-container {
    margin: 10px 10px 10px 10px;
    padding: 10px 10px 10px 10px;
  }
</style>



